import React from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Navbar from "./components/Navbar";
import Home from "./pages/Home/Home";
import Projects from "./pages/Projects/Projects";
import Contact from "./pages/Contact/Contact";
import Resum from "./pages/Resum/Resum";
import Life from "./pages/Life/Life";

function App() {
    return (
        <Router>
            <Navbar />
            <div className="page-content">
                <Routes>
                    <Route path="" element={<Home className="home-page" />} />
                    <Route path="/projects" element={<Projects className="projects-page" />} />
                    <Route path="/contact" element={<Contact className="contact-page" />} />
                    <Route path="/resum" element={<Resum className="resume-page" />} />
                    <Route path="/life" element={<Life className="life-page" />} />
                </Routes>
            </div>
        </Router>
    );
}

export default App;
